"use client";

import { useState } from "react";
import CandleChart from "@/components/CandleChart";

export default function Dashboard() {
  const [signal, setSignal] = useState(null);

  const runEmaStrategy = async () => {
    try {
      const res = await fetch("http://localhost:8000/strategy/ema-signal");
      const data = await res.json();
      setSignal(data.signal);
    } catch (error) {
      console.error("Error fetching signal:", error);
      setSignal("Error fetching signal");
    }
  };

  return (
    <main style={{ padding: "2rem", fontFamily: "sans-serif" }}>
      <h1>ðŸ“Š Trading Dashboard</h1>
      <p>You are logged in. Choose an action:</p>

      <button
        onClick={runEmaStrategy}
        style={{
          marginTop: "1rem",
          padding: "10px 20px",
          backgroundColor: "#28a745",
          color: "white",
          border: "none",
          borderRadius: "6px",
          cursor: "pointer",
        }}
      >
        Run EMA Crossover Strategy
      </button>

      {signal && (
        <p style={{ marginTop: "1rem", fontSize: "18px" }}>
          ðŸ“£ Signal: <strong>{signal}</strong>
        </p>
      )}
    </main>
  );
}
